
import { defineComponent, inject, ref, reactive, computed, watch } from 'vue';
import Modal from '@/components/Modal.vue';
import Dropdown from '@/components/Dropdown.vue';
import ModalSelectItem from '@/components/ModalSelectItem.vue';
import { Datastore, Item, Unit } from '@/datastore';
import { DateTime } from 'luxon'

interface DetailItem {
  itemID: number
  itemCode: string
  itemName: string
  itemUnitName: string
  itemPrice: number
  quantity: number
  discountPercentage: number
  discountAmount: number
  amount: number
}

export default defineComponent({
  components: {
    Modal,
    Dropdown,
    ModalSelectItem,
  },
  setup () {
    const datastore = inject<Datastore>('datastore')!
    const units = ref<Unit[]>([])
    datastore.unit.getAllUnits()
      .then(x => {
        units.value = x
      })
    const items = ref<Item[]>([])
    datastore.item.getAllItems()
      .then(x => {
        items.value = x
      })

    const today = DateTime.now().set({ hour: 0, minute: 0, second: 0, millisecond: 0 })
    const todayText = computed(() => today.toFormat('dd/MM/yyyy'))
    const detailItems = ref<DetailItem[]>([])
    const subtotalBeforeDiscount = computed(() => {
      let sum = 0
      for (const { itemPrice, quantity } of detailItems.value)
        sum += itemPrice * quantity
      return sum
    })
    const totalItemDiscount = computed(() => {
      let sum = 0
      for (const { discountAmount } of detailItems.value)
        sum += discountAmount
      return sum
    })
    const subtotal = computed(() => subtotalBeforeDiscount.value - totalItemDiscount.value)
    const tradeDiscount = ref(0)
    const grandTotal = computed(() => Math.max(subtotal.value - tradeDiscount.value, 0))

    const modalSelectItem = reactive({
      requestNum: 0,
      initialSelectedID: undefined! as number,
      rowIndex: 0,
    })
    function openModalSelectItem(index: number) {
      modalSelectItem.rowIndex = index
      modalSelectItem.initialSelectedID = index || index === 0 ? detailItems.value[index].itemID : undefined!
      modalSelectItem.requestNum++
    }
    function onSelectItem(itemID: number) {
      const item = items.value.find(x => x.id === itemID)!
      let detailItem: DetailItem = reactive({
        itemID: item.id!,
        itemCode: item.code,
        itemName: item.name,
        itemUnitName: units.value.find(x => x.id === item.unitID)!.name,
        itemPrice: item.price,
        quantity: 0,
        discountPercentage: 0,
        discountAmount: computed(() => Math.min(detailItem.discountPercentage / 100, 1) * (detailItem.itemPrice * detailItem.quantity)),
        amount: computed(() => detailItem.itemPrice * detailItem.quantity - detailItem.discountAmount),
      })
      if (modalSelectItem.rowIndex !== null && modalSelectItem.rowIndex !== undefined) {
        detailItems.value[modalSelectItem.rowIndex] = detailItem
      }
      else {
        detailItems.value.push(detailItem)
      }
    }
    function onSubmit() {
      const data = detailItems.value
      for (const detailItem of data) {
        if (!detailItem.quantity) {
          alert('quantity is empty')
          return
        }
      }

      datastore.receipt.addReceipt({
        details: data.map(x => ({
          itemID: x.itemID,
          itemCode: x.itemCode,
          itemName: x.itemName,
          itemUnitName: units.value.find(y => y.id === items.value.find(z => z.id === x.itemID)!.unitID!)?.name!,
          itemPrice: x.itemPrice,
          quantity: x.quantity,
          discountAmount: x.discountAmount,
          discountPercentage: x.discountPercentage,
          amount: x.amount,
        })),
        subtotalBeforeDiscount: subtotalBeforeDiscount.value,
        totalItemDiscount: totalItemDiscount.value,
        subtotal: subtotal.value,
        tradeDiscount: tradeDiscount.value,
        grandTotal: grandTotal.value,
      })
        .then(async () => {
          detailItems.value = []
        })
        .catch(x => alert(x))
    }
    function onDelete(index: number) {
      detailItems.value.splice(index, 1)
    }

    return {
      items,
      todayText,
      detailItems,
      subtotalBeforeDiscount,
      totalItemDiscount,
      subtotal,
      tradeDiscount,
      grandTotal,
      modalSelectItem,
      openModalSelectItem,
      onSelectItem,
      onSubmit,
      onDelete,
    }
  },
});
